const checkedModuleCount = computed(() => items.value.filter((item) => item.type == "module" && item.allChildChecked).length);
const saveButtonDisable = computed(() => !items.value.some((item) => item.checked || item.anyChildChecked));

function handleItems() {
    items.value = viewModel.studio.items
        .filter((item) => item.objectType == "module" && item.children.length > 0)
        .map(
            (item) =>
            ({
                id: item.ID,
                collapsed: true,
                name: item.name,
                type: "module",
                anyChildChecked: update ? (item as IModule).children.some(item => selectedModelIDs.indexOf(item.ID) != -1) : false,
                allChildChecked: update ? (item as IModule).children.every(item => selectedModelIDs.indexOf(item.ID) != -1) : false,
                children: (item as IModule).children
                    .filter((child) => child.objectType == "model")
                    .map(
                        (child) =>
                        ({
                            id: child.ID,
