                            checked: update ? selectedModelIDs.indexOf(item.ID) != -1 : false,
                            name: child.name,
                            type: (child as IModel).modelType,
                            version: "latest",
                        } as ITableData)
                    ),
            } as ITableData)
        );
    viewModel.studio.items
        .filter((item) => item.objectType == "model" && item.usageType != "appSettings")
        .forEach((item) => {
            items.value.push({
                id: item.ID,
                checked: update ? selectedModelIDs.indexOf(item.ID) != -1 : false,
                name: item.name,
                type: (item as IModel).modelType,
                version: "latest",
            });
        });
